.wrapper {
  width: 100%;
  max-height: 616px;
  overflow: auto;
}

.list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 0;
  margin-left: -12px;
  padding: 0;
}

.list__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  position: relative;
}

.card {
  color: #F2F2F3;
  text-decoration: none;
}